body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {
  text-align: center;
}

body {
  padding: 0;
  margin: 0;
}
.navbar {
    background-color: #03396c;
    height: 10vh;
    width: 100%;
    position: fixed;
    top: 0px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 1;
}

.logo {
    color: white;
    font-size: 50px;
    font-weight: 100;
    padding-left: 50px;
}

#open .logo {
    display: none;
}

.links {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: horizontal;
    height: 100%;
    width: 50%;
}

.links a {
    color: white;
    text-decoration: none;
    font-size: 16px;
    font-weight: 500;
}

#open {
    height: 100vh;
    width: 70vw;
    margin-left: 30vw;
}

#open .links {
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#open .links a {
    padding-bottom: 40px;
}

.language {
    color: white;
    flex: -1 1;
    display: flex;
    margin-left: 35vw;
    font-size: 12px;
    font-weight: 500;
}

#open .language {
    display: none;
}

.toggleButton {
    height: 100%;
    display: flex;
    justify-content: flex-end;
}

.toggleButton button{
    background-color: transparent;
    border: none;
    color: white;
    cursor: pointer;
    margin-right: 10px;
}

.toggleButton svg {
    font-size: 40px;
    height: 100%;
}

#open .toggleButton {
    /* display: none; */
    position: absolute;
    top: 0;
    right: 0;
    height: 10vh;
}

@media screen and (min-width: 600px) {
    #close .toggleButton {
        display: none;
    }
}

@media screen and (max-width: 600px) {
    #close .links {
        display: none;
    }
}
.home {
    padding-top: 10vh;
}
.area {
    /* background-color: red; */
    position: relative;
    display: flex;
    padding: 0;
    margin: 0;
    text-align: left;
}

.anchor {
    position: relative;
    top: -20vh;
}

.content {
    color: white;
    position: absolute;
    left: 15vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 70vw;
    min-height: 300px;
}

.content .welcome {
    padding: 0;
    margin: 0;
    font-size: 12px;
    font-weight: 500;
}

.content .main {
    margin-top: 0.5vh;
    font-weight: 500;
}

.content .character {
    color: orange;
    font-weight: 800;
}

.content .desc {
    margin-top: 1vh;
    font-size: 12px;
}

.image {
    width: 100vw;
    padding: 0;
    margin: 0;
    min-height: 300px;
}
.footer {
    background: #21325e;
    color: white;
    display: flex;
    flex-direction: column;
}
.projectCard {
    display: flex;
    text-align: left;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    position: relative;
    width: 30vw;
    height: 40vw;
    border: 1px solid blue;
    overflow: hidden;
}

.projectCard .cardImage {
    width: 100%;
    height: 80%;
    object-fit: cover;
    /* aspect-ratio: '3/2'; */
}
.projects {
    margin: 100px;
    padding: 100px;
}
